<script lang='ts'>
  import { AsYouType, findPhoneNumbersInText } from 'libphonenumber-js';
  import { objErr, objInput, countryNumber } from "./validator.js";

  const formatterNumber = (e: any) => {
    let str: any = $countryNumber;
    let city: any = findPhoneNumbersInText($objInput.data)[0];
    $objInput.data = new AsYouType(str).input(e.target.value);
    if (city !== undefined) $countryNumber = city.number.country;
  };

  const pasteNumber = () => {
    let city: any = findPhoneNumbersInText($objInput.data)[0];
    if (city !== undefined) $countryNumber = city.number.country;
  }
</script>

<form on:submit novalidate>
  <label for='cbu'>Insert your CBU or CVU</label>
  <input bind:value={$objInput.cbu} name='cbu' placeholder='Insert CBU or CVU' autocomplete='off'>
  {#if $objErr.errorCbu !== undefined}
    <span class='err-cbu'>{$objErr.errorCbu}</span>
  {/if}
  <label for='date'>Insert your Number</label>
  <div>
    <input on:keyup={formatterNumber} on:focus={pasteNumber} on:blur={pasteNumber} bind:value={$objInput.data} name='date' placeholder='Insert Number.....' autocomplete='off'>
    <select bind:value={$countryNumber}>
      <option value='AR'>AR</option>
      <option value='BO'>BO</option>
    </select>
  </div>
  {#if $objErr.errorData !== undefined}
    <span class='err-data'>{$objErr.errorData}</span>
  {/if}
  <button type='submit'>Continue</button>
</form>

